.TitleBar {
  display: flex;
  height: 100%;
  padding: 0 15px;
  color: white;
  font-size: 1.5em;
  font-family: 'title-bar';
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 0 16px darken(gray,10%);
  -webkit-app-region: drag;
  .Icon{
    width: 35px;
    height: 35px;
  }
  .Title {
    font-size: 20px;
    font-family: 'title-bar';
    color: white;
  }
  .TitleBtnGroup {
    display: flex;
    flex-direction: row-reverse;
    justify-content: right;

    .TBtn {
      width: 38px;
      height: 38px;
      margin: 0 5px;
      cursor: pointer;
      -webkit-app-region: no-drag;
    }
  }
}
